<template>
  <div>
    <LayoutBox one="邮费管理" two="小超邮费规则">
      <div v-if="shopPostage">
        <p>商家承担运费的{{ shopPostage.shopPostage }}%</p>
        <p>起送价{{ shopPostage.sendingFee ? shopPostage.sendingFee : 20 }}元</p>
        <p v-if="shopPostage.packageMail">满{{ shopPostage.packageMail }}元包邮</p>
        <el-button type="primary" size="small" @click="editPostfeeVisible = true">修改</el-button>
      </div>
      <div v-else>
        <span>暂无规则</span>
        <el-button type="primary" size="small" @click="addPostfeeVisible = true">添加</el-button>
      </div>
    </LayoutBox>

    <add-postfee-cmp :dialogVisible="addPostfeeVisible" @close="addPostfeeVisible = false" />

    <edit-postfee-cmp :dialogVisible="editPostfeeVisible" :form="form" @close="editPostfeeVisible = false" />
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import editPostfeeCmp from './edit_postfee_cmp/index.vue'
import addPostfeeCmp from './add_postfee_cmp/index.vue'
import { MIXINS_SUPERGETCOST_API } from '@/mixins/api'

export default {
  name: 'postFeeSupermarket',

  components: {
    LayoutBox,
    editPostfeeCmp,
    addPostfeeCmp,
  },

  mixins: [
    MIXINS_SUPERGETCOST_API
  ],

  data () {
    return {
      editPostfeeVisible: false,
      addPostfeeVisible: false,
      shopPostage: {},
      form: {},
    }
  },

  created () {
    this.apiSuperGetCost()
  },

  methods: {
    // 私有方法-----------------------------
    // 获取小超邮费规则
    apiSuperGetCost () {
      this.superGetCost({}).then((res) => {
        console.log(res)
        if (res.data.success) {
          this.shopPostage = res.data.data
          this.form = JSON.parse(JSON.stringify(this.shopPostage))
        } else {
          this.$message.error(res.data.msg)
        }
      })
    }
  }
}
</script>

