<template>
  <div>
    <el-dialog
      title="添加商家承担运费的比例"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <el-form ref="ruleForm" :rules="rules" :model="form">
        <el-form-item label="商家承担（100表示包邮）" prop="shopPostage">
          <el-input v-model="form.shopPostage">
            <template slot="append">
              <span>%</span>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="起送价（默认20元起送）">
          <el-input v-model="form.sendingFee">
            <template slot="append">
              <span>元</span>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="满多少钱包邮（0元表示全部包邮，默认全部不包邮）">
          <el-input v-model="form.packageMail">
            <template slot="append">
              <span>元</span>
            </template>
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="onSave">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { MIXINS_SUPERADDSHOPPOSTAGE_API } from '@/mixins/api'
import { MIXINS_BASIC_FORMRULES } from '@/mixins/form'

export default {
  name: 'addPostfeeCmp',

  mixins: [
    MIXINS_SUPERADDSHOPPOSTAGE_API,
    MIXINS_BASIC_FORMRULES
  ],

  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    },
  },

  data() {
    return {
      form: {
        shopPostage: '',
      },
      rules: {
        shopPostage: [
          { required: true, message: '请输入商家承担的比例', trigger: 'blur' },
        ]
      }
    }
  },

  methods: {
    handleClose() {
      this.$emit('close')
    },

    onSave () {
      if (!this._ruleForm('ruleForm')) return false
      const data = {
        ...this.form
      }
      this.superAddShopPostage(data).then((res) => {
        console.log(res)
        if (res.data.success) {
          this.handleClose()
          this.reload()
          this.$message.success('保存成功')
        } else {
          this.$message.error(res.data.msg)
        }
      })
    }
  },

  inject: ['reload']
}
</script>

<style lang="scss" scoped>

</style>
